.rc-menu-container {
  position: absolute;
  width: 0px;
  height: 0px;
}

.rc-menu {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 1rem;
  color: #212529;
  background-color: #fff;
  border-radius: 0.25rem;
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.133), 0 0.6px 2px rgba(0, 0, 0, 0.1);
  position: absolute;
  z-index: 100;
  min-width: 10rem;
  padding: 0.5rem 0;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  cursor: default;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: none;
}
.rc-menu:focus {
  outline: none;
}
.rc-menu--open, .rc-menu--closing {
  display: block;
}
.rc-menu--open.rc-menu--dir-left {
  -webkit-animation: slide-out-left 0.15s ease-out;
          animation: slide-out-left 0.15s ease-out;
}
.rc-menu--closing.rc-menu--dir-left {
  -webkit-animation: slide-in-left 0.15s ease-in;
          animation: slide-in-left 0.15s ease-in;
}
.rc-menu--open.rc-menu--dir-right {
  -webkit-animation: slide-out-right 0.15s ease-out;
          animation: slide-out-right 0.15s ease-out;
}
.rc-menu--closing.rc-menu--dir-right {
  -webkit-animation: slide-in-right 0.15s ease-in;
          animation: slide-in-right 0.15s ease-in;
}
.rc-menu--open.rc-menu--dir-top {
  -webkit-animation: slide-out-top 0.15s ease-out;
          animation: slide-out-top 0.15s ease-out;
}
.rc-menu--closing.rc-menu--dir-top {
  -webkit-animation: slide-in-top 0.15s ease-in;
          animation: slide-in-top 0.15s ease-in;
}
.rc-menu--open.rc-menu--dir-bottom {
  -webkit-animation: slide-out-bottom 0.15s ease-out;
          animation: slide-out-bottom 0.15s ease-out;
}
.rc-menu--closing.rc-menu--dir-bottom {
  -webkit-animation: slide-in-bottom 0.15s ease-in;
