.rc-menu__item {
  position: relative;
  display: flex;
  align-items: center;
  font-weight: 400;
  padding: 0.375rem 1.5rem;
  background-color: #fff;
  cursor: pointer;
}
.rc-menu__item:focus {
  outline: none;
}
.rc-menu--animation .rc-menu__item {
  transition-property: background-color, color;
  transition-duration: 0.15s;
  transition-timing-function: ease-in-out;
}
.rc-menu__item--anchor {
  display: block;
}
.rc-menu__item--hover {
  background-color: #ebebeb;
}
.rc-menu__item--active {
  color: #fff;
  background-color: #007bff;
}
.rc-menu__item--active:hover {
  color: #fff;
}
.rc-menu__item--disabled {
  color: #aaa;
  cursor: default;
}
.rc-menu__item--type-radio {
  padding-left: 2.2rem;
}
.rc-menu__item--type-radio::before {
  content: "○";
  position: absolute;
  left: 0.8rem;
  top: 0.55rem;
  font-size: 0.8rem;
}
.rc-menu__item--type-radio.rc-menu__item--checked::before {
  content: "●";
}
.rc-menu__item--type-checkbox {
  padding-left: 2.2rem;
}
.rc-menu__item--type-checkbox::before {
  position: absolute;
  left: 0.8rem;
}
.rc-menu__item--type-checkbox.rc-menu__item--checked::before {
  content: "✔";
}
.rc-menu__submenu {
  position: relative;
}
.rc-menu__submenu > .rc-menu__item {
  padding-right: 2.5rem;
}
.rc-menu__submenu > .rc-menu__item::after {
  content: "❯";
  position: absolute;
  right: 1rem;
}
.rc-menu__radio-group {
  margin: 0;
  padding: 0;
  list-style: none;
}
.rc-menu__header {
  color: #888;
  font-size: 0.8rem;
  padding: 0.2rem 1.5rem;
  text-transform: uppercase;
}
.rc-menu__divider {
  height: 1px;
  margin: 0.5rem 0;
  background-color: rgba(0, 0, 0, 0.12);
}

