  font-size: 1rem;
  font-weight: 400;
  color: #212529;
  line-height: 1.5;
  padding: 0.375rem 0.75rem;
  border: 1px solid rgba(33, 37, 41, 0.5);
  border-radius: 0.25rem;
  background-color: #fff;
  transition: background-color 0.12s ease-in-out;
